/*----Login Page Animations----*/

.insta-button:hover {
  -webkit-animation: insta-button-buldger 1s forwards;
          animation: insta-button-buldger 1s forwards;
  background-color: white;
}

@-webkit-keyframes insta-button-buldger {
  0% {
    font-size: 100%;
  }
  100% {
    font-size: 110%;
    background-color: pink;
    border-color: white;
  }
}

@keyframes insta-button-buldger {
  0% {
    font-size: 100%;
  }
  100% {
    font-size: 110%;
    background-color: pink;
    border-color: white;
  }
}

.Cactus-logo:hover {
  -webkit-animation: Cactus-logo-spin infinite 3s linear;
          animation: Cactus-logo-spin infinite 3s linear;
}

@-webkit-keyframes Cactus-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(25deg);
            transform: rotate(25deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  75% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes Cactus-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  25% {
    -webkit-transform: rotate(25deg);
            transform: rotate(25deg);
  }
  50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  75% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}


/*-----------------*/


/*---SIDE NAV-----*/

.Side-nav {
  display: inline;
}

.Sidebar-demo {
  padding: 15px;
}

.Sidebar.modal.fade .modal-dialog {
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

.Sidebar.modal.fade {
  -webkit-transition: opacity 0.15s linear, -webkit-transform 0.3s ease-out;
  transition: opacity 0.15s linear, -webkit-transform 0.3s ease-out;
  -o-transition: transform 0.3s ease-out, opacity 0.15s linear;
  transition: transform 0.3s ease-out, opacity 0.15s linear;
  transition: transform 0.3s ease-out, opacity 0.15s linear, -webkit-transform 0.3s ease-out;
}

.Sidebar.modal.fade:not(.in).left {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}

.Sidebar.modal.fade:not(.in).right {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}

.Sidebar.modal {
  /* overflow: auto; */
  max-width: 100%;
  -webkit-box-shadow: 5px 0 10px;
          box-shadow: 5px 0 10px;
  background-color: #f5f5f5;
}

.Sidebar.modal.left {
  right: auto;
}

.Sidebar.modal.right {
  left: auto;
}

.Sidebar.modal .modal-dialog {
  margin: 0;
  width: auto;
}

.Sidebar.modal .modal-dialog .modal-content {
  background-color: transparent;
  border: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.Sidebar.modal .modal-dialog .modal-content .modal-body {
  padding: 0;
}

.Sidebar.modal.fade:not(.in).left .modal-dialog {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}

.Sidebar.modal.fade:not(.in).right .modal-dialog {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}

.Sidebar .nav {
  width: 320px;
}

