/*ToDo List*/

.sticky {
  /* General */
  margin: 0 auto;
  padding: 8px 24px;
  width: 380px;
  /* Font */
  font-family: 'Shadows Into Light Two', cursive !important;
  font-size: 1.4em;
  /* Border */
  border: 1px #E8Ds47 solid;
  /* Shadow */
  -webkit-box-shadow: 0px 0px 6px 1px #333333;
  box-shadow: 0px 0px 6px 1px #333333;
  /* Background */
  background: rgba(247, 110, 67, 0.64);
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(247, 110, 67, 0.64)), color-stop(100%, #ff5722));
  background: -webkit-linear-gradient(top, rgba(247, 110, 67, 0.64) 0%, #ff5722 100%);
  background: -o-linear-gradient(top, rgba(247, 110, 67, 0.64) 0%, #ff5722 100%);
  background: -ms-linear-gradient(top, rgba(247, 110, 67, 0.64) 0%, #ff5722 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='rgba(247, 110, 67, 0.64)', endColorstr='#ff5722', GradientType=0);
  background: -webkit-gradient(linear, left top, left bottom, from(top), color-stop(0%, rgba(247, 110, 67, 0.64)), to(#ff5722));
  background: linear-gradient(top, rgba(247, 110, 67, 0.64) 0%, #ff5722 100%);
}

.toDoHolder {
  width: 175px;
  margin-right: 10px;
}

.stickyHR {
  border: 0;
  height: 1.5px;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0)), color-stop(rgba(0, 0, 0, 0.75)), to(rgba(0, 0, 0, 0)));
  background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  background-image: -o-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.sticky ul {
  list-style-type: none;
  margin: 12px;
}

.sticky p {
  text-align: center;
}

.toDoListItem {
  margin: none;
}


/*Image*/

.instagram {
  height: 350px;
  width: 350px;
  margin-left: -15px;
}

.stackone {
  border: 6px solid #fff;
  float: left;
  height: 362px;
  width: 362px;
  margin: 50px 93px;
  position: relative;
  -webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.stackone:before {
  content: "";
}

.stackone:before {
  content: "";
  height: 362px;
  width: 362px;
  background: #eff2bb;
  border: 6px solid #fff;
  position: absolute;
  z-index: -1;
  top: 0px;
  left: -10px;
  -webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  -webkit-transform: rotate(-5deg);
  -ms-transform: rotate(-5deg);
  transform: rotate(-5deg);
}

.stackone:after {
  content: "";
  height: 362px;
  width: 362px;
  background: #768590;
  border: 6px solid #fff;
  position: absolute;
  z-index: -1;
  top: 5px;
  left: 0px;
  -webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  -webkit-transform: rotate(4deg);
  -ms-transform: rotate(4deg);
  transform: rotate(4deg);
}

.stackone, blockquote {
  margin: 50px 55px;
}

blockquote {
  background: #f9f9f9;
  border-left: 10px solid #A7E8BD;
  margin: 1.5em 10px;
  padding: -1.5em 10px;
  margin-top: 110px;
  quotes: "\201C""\201D""\2018""\2019";
}

blockquote:before {
  color: #A7E8BD;
  content: open-quote;
  font-size: 5em;
  line-height: 0.1em;
  margin-right: 6.25em;
  vertical-align: -0.5em;
}

blockquote p {
  display: inline;
}

.quotebtn {
  margin-right: -250px;
  height: 28px;
}

.quotebtn, .quotebtn:visited {
  color: #fff;
  background-color: rgba(40, 96, 144, 0.5);
  border-color: transparent;
}

.quotebtn:hover {
  color: #fff;
  background-color: rgba(40, 96, 144, 0.75);
  border-color: rgba(32, 77, 116, 0.75);
}

.quotebtn:active {
  color: #fff;
  background-color: #286090;
  border-color: #204d74;
}



